import React from 'react'
import loadable from '@loadable/component'
import pMinDelay from 'p-min-delay'
import Loader from './shared/Loader'

// Layouts
import Layout1 from './shared/layouts/Layout1'

// Lazy load component
const lazy = (cb) => loadable(() => pMinDelay(cb(), 200), { fallback: <Loader /> })

// ---
// Default application layout

export const DefaultLayout = Layout1

// ---
// Document title template

export const titleTemplate = '%s - WAMSPORT UI'

// ---
// Routes
//
// Note: By default all routes use { "exact": true }. To change this
// behaviour, pass "exact" option explicitly to the route object

export const defaultRoute = '/'
export const routes = [
  {
    path: '/',
    component: lazy(() => import('./components/Home'))
  }, {
    path: '/signup',
    component: lazy(() => import('./components/Signup'))
  }, {
    path: '/signup-sport-extra',
        component: lazy(() => import('./components/SignupSportExtra'))
}, {
    path: '/signup-sport',
        component: lazy(() => import('./components/SignupSport'))
}, {
    path: '/forgot-password',
        component: lazy(() => import('./components/ForgotPassword'))
  }, {
    path: '/reset-password',
        component: lazy(() => import('./components/ResetPassword'))
}, {
    path: '/forgot-password-success',
        component: lazy(() => import('./components/ForgotPasswordSuccess'))
}, {
    path: '/contact-us',
        component: lazy(() => import('./components/ContactUs'))
}, {
    path: '/contact-us-success',
        component: lazy(() => import('./components/ContactUsSuccess'))
}, {
    path: '/user-view',
        component: lazy(() => import('./components/UserView'))
}, {
    path: '/user-edit',
        component: lazy(() => import('./components/UserEdit'))
}, {
    path: '/dashboard',
        component: lazy(() => import('./components/Dashboard'))
}, {
    path: '/sport-edit',
        component: lazy(() => import('./components/SportEdit'))
}, {
    path: '/find-sport',
        component: lazy(() => import('./components/FindSport'))
}, {
    path: '/find-sport-results',
        component: lazy(() => import('./components/FindSportResults'))
}, {
    path: '/sport-details',
        component: lazy(() => import('./components/SportDetails'))
}
]
