import React, { Component } from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import Sidenav from '../../vendor/libs/sidenav'
import layoutHelpers from './helpers'

class LayoutSidenav extends Component {
  layoutSidenavClasses() {
    let bg = this.props.sidenavBg

    if (this.props.orientation === 'horizontal' && (bg.indexOf(' sidenav-dark') !== -1 || bg.indexOf(' sidenav-light') !== -1)) {
      bg = bg
        .replace(' sidenav-dark', '')
        .replace(' sidenav-light', '')
        .replace('-darker', '')
        .replace('-dark', '')
    }

    return `bg-${bg} ` + (
      this.props.orientation !== 'horizontal'
        ? 'layout-sidenav'
        : 'layout-sidenav-horizontal container-p-x flex-grow-0'
    )
  }

  toggleSidenav(e) {
    e.preventDefault()
    layoutHelpers.toggleCollapsed()
  }

  isMenuActive(url) {
    return this.props.location.pathname.indexOf(url) === 0
  }

  isMenuOpen(url) {
    return this.props.location.pathname.indexOf(url) === 0 && this.props.orientation !== 'horizontal'
  }

  render() {
    return (
      <Sidenav orientation={this.props.orientation} className={this.layoutSidenavClasses()}>
        {/* Inner */}
        <div className={`sidenav-inner ${this.props.orientation !== 'horizontal' ? 'py-1' : ''}`}>

          <Sidenav.RouterLink to="/" exact={true} icon="ion ion-ios-contact">Home</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/signup" exact={true} icon="ion ion-md-desktop">Signup</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/signup-sport" exact={true} icon="ion ion-md-desktop">Signup Sport</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/signup-sport-extra" exact={true} icon="ion ion-md-desktop">Signup Sport Extra</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/forgot-password" exact={true} icon="ion ion-md-desktop">Forgot Password</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/forgot-password-success" exact={true} icon="ion ion-md-desktop">Forgot Password Success</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/reset-password" exact={true} icon="ion ion-md-desktop">Reset Password</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/contact-us" exact={true} icon="ion ion-md-desktop">Contact Us</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/contact-us-success" exact={true} icon="ion ion-md-desktop">Contact Us Success</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/user-view" exact={true} icon="ion ion-md-desktop">Profile View</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/user-edit" exact={true} icon="ion ion-md-desktop">Profile Edit</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/dashboard" exact={true} icon="ion ion-md-desktop">Dashboard</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/sport-edit" exact={true} icon="ion ion-md-desktop">Sport Edit</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/find-sport" exact={true} icon="ion ion-md-desktop">Find Sport</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/find-sport-results" exact={true} icon="ion ion-md-desktop">Find Sport Results</Sidenav.RouterLink>
          <Sidenav.RouterLink to="/sport-details" exact={true} icon="ion ion-md-desktop">Sport Details</Sidenav.RouterLink>

        </div>
      </Sidenav>
    )
  }
}

LayoutSidenav.propTypes = {
  orientation: PropTypes.oneOf(['vertical', 'horizontal'])
}

LayoutSidenav.defaultProps = {
  orientation: 'vertical'
}

export default connect(store => ({
  sidenavBg: store.theme.sidenavBg
}))(LayoutSidenav)
